/* src/Education.css */
.education-page {
    background-color: #212a31; /* Dark background */
    color: #d3d9d4; /* Light text */
    padding-top: 60px;
    min-height: 100vh; /* Full height */
  }
  
  .education-content {
    text-align: center;
    margin-top: 50px;
  }
  
  .education-content h1 {
    font-size: 36px;
    color: #d3d9d4; /* Light color for the title */
  }
  
  .education-content p {
    font-size: 18px;
    color: #d3d9d4; /* Light color for text */
  }
  
  .continue-button {
    padding: 12px 30px;
    font-size: 16px;
    background-color: #124e66; /* Dark blue button */
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 20px;
  }
  
  .continue-button:hover {
    color: #2e3944; /* Dark hover effect */
  }
  