/* src/NavBar.css */
.navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #13191d; /* Dark background to match the color scheme */
    padding: 10px 0;
    z-index: 1000; /* Make sure the navbar stays on top */
  }
  
  .navbar-list {
    list-style: none;
    display: flex;
    justify-content: center; /* Center items horizontally */
    margin: 0;
    padding: 0;
  }
  
  .navbar-item {
    margin: 0 20px; /* Add space between items */
  }
  
  .navbar-link {
    text-decoration: none;
    color: #d3d9d4; /* Light color for the text */
    font-size: 18px;
    padding: 10px 20px;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .navbar-link:hover {
    background-color: #124e66; /* Dark blue background on hover */
  }
  
  .navbar-link.active {
    background-color: #124e66; /* Highlight the active link with a green background */
  }
  