/* src/Projects.css */
.projects-page {
  background-color: #212a31; /* Dark background color */
  color: #d3d9d4; /* Light text color */
  padding-top: 60px; /* Prevent content from hiding behind the navbar */
  min-height: 100vh; /* Full height */
  display: flex;
  flex-direction: column; /* Stack the content vertically */
  justify-content: flex-start; /* Align content to the top */
  align-items: center; /* Center the content horizontally */
}

.projects-content {
  text-align: center;
  margin-top: 50px;
}

.projects-content h1 {
  font-size: 36px;
  color: #d3d9d4; /* Light color for the title */
}

.projects-content p {
  font-size: 18px;
  color: #d3d9d4; /* Light color for text */
}

/* This class applies to the container holding the project divs */
.Projects.Area {
  display: flex;
  flex-direction: column; /* Align projects vertically */
  align-items: center;
  width: 100%; /* Ensure it takes up the full width */
}

/* Each individual Project */
.Project {
  display: flex;
  flex-direction: row; /* Align left and right sides horizontally */
  justify-content: center; /* Center the content horizontally */
  align-items: flex-start; /* Align top edges of content */
  margin-bottom: 60px; /* Space between each project */
  width: 80%; /* Limit width to make sure it doesnâ€™t stretch too wide */
  max-width: 1200px; /* Max width for larger screens */
  gap: 20px; /* Add gap between the left and right sides */
}

.left-side {
  margin-right: 20px; /* Add some space between the text and the image */
  max-width: 50%; /* Allow for more space for text */
}

.right-side {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 50%; /* Allow for image alignment */
}

.right-side img {
  width: 200px; /* Set the width of the image */
  height: 200px; /* Set the height of the image to match the width */
  object-fit: cover; /* Ensures the image covers the entire square without stretching */
  border-radius: 10px; /* Optional: keeps rounded corners if desired */
}

.continue-button {
  padding: 12px 30px;
  font-size: 16px;
  background-color: #124e66; /* Dark blue button */
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 20px;
}

.continue-button:hover {
  color: #2e3944; /* Dark hover effect */
}
